.footer {
  padding: $space-2;
  background-image: linear-gradient(-219deg, #377de3 0%, #1156ba 100%);
  hr {
    background-color: $white;
  }
  ul {
    padding: 0 13px;
  }
  &__social-networks {
    display: flex;
    flex-direction: row;
    padding-bottom: 10px;
    ul {
      display: flex;
      flex-direction: row;
    }
    a {
      padding-right: 13px;
      font-weight: 600;
      color: $white;
    }
  }
  &__footer-links {
    display: flex;
    a {
      color: $white;
    }

  }
  &__copyrights {
    display: flex;
    p {
      margin-left:auto;
      color: $white;
    }
  }
}
